body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Total {
  min-width: 1100px;
}
.Title {
  display: flex;
  text-align: left;
  background: white;
  color: rgb(49, 49, 49);
  font-size: 36px;
  padding: 2rem;
  font-weight: 900;
}

.Body {
  flex-direction: column;
  align-items: center;
  background: #492b56;
  min-height: 80em;
}

.Bodyed {
  height: 100vh;
  background: #2c1c3e;
  align-self: center;
  align-items: center;
  justify-content: center;
  box-shadow: 0px -5px 0px 0px rgba(78, 0, 167, 0.274);
}

.BG {
  display: flex;
  align-self: center;
  align-items: center;
  justify-content: center;
}

.MySelf {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-Top: 250px;
  align-self: center;
  height: 500px;
  width: 500px;
  border-radius: 250px;
  background-color: #ffffff;
  flex-direction: column;
  box-shadow: 0px 0px 0px 10px #2c1c3e;
 
}

.MyselfContent {
  justify-content: center;
  flex-direction: column;
  text-align: center;
}

.PhoneNumber {
  color: rgb(255, 255, 255);
  font-size: 18px;
  font-weight: 500;
  margin-left: 20px;
}

.Input {
  color: white;
  margin-bottom: 20px;
  margin-left: 25px;
}

.SNS {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 500px;
  height: 150px;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  border-bottom-left-radius: 150px;
  border-bottom-right-radius: 150px;
  align-self: center;
  background: #2c1c3e;
}

.Welcome {
  padding-top : 40px;
  font-size: 55px;
  text-align: center;
  font-weight: 700;
  color: white;

}

.Contents {
  padding-top : 200px;
  font-size: 17px;
  text-align: left;
  font-weight: 200;
  color: white;
  padding-left: 250px;
  padding-right: 250px;
}

